<script>
export default {
  name: 'about',
  data () {
    return {
      birthday: '12-06-1996'
    }
  },
  computed: {
    age: function () {
      let diff = new Date(Date.now() - new Date(this.birthday))

      return Math.abs(diff.getUTCFullYear() - 1970)
    }
  }
}
