// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
window.$ = require('jquery')

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  template: '<App/>',
  components: { App }
})

$(() => {
  /*
   * Variables
   */
  var MsgButton = $('#msg-button')
  var Response = $('#response')
  var ResponseText = $('#response-text')
  var Send = true

  /*
   * Scrolling and menu
   */
  $('.menu .item').first().toggleClass('active', 2000)

  $('.menu .item').click(function () {
    $('.active').removeClass('active')
    $(this).toggleClass('active', 300)
  })

  $('.scroll').on('click', function () {
    $('html,body').animate({
      scrollTop: $($(this).attr('href')).offset().top
    }, 600)
  })

  /*
   * Contact
   */
  $('.select-me').click(function () {
    $(this).select()
  })

  $('form').submit(function (e) {
    e.preventDefault()

    let Msg = $(this).find('textarea').val()
    let From = $('#email').val()
    let Response = $(this).find('p')

    if (Msg !== '' && From !== '') {
      $.ajax({
        method: 'POST',
        url: 'https://bausano.000webhostapp.com/email.php',
        data: {
          from: From,
          msg: Msg
        }
      }).done(function (rtn) {
        if (rtn !== 'true') {
          $(Response).fadeIn().text(rtn)
        }
      })
    } else {
      $(Response).fadeIn().text('You have to fill in both message and email!')
    }
  })

  /*
   * Instant message
   */
  $(MsgButton).click(function () {
    if (!Send) {
      return false
    }

    let Msg = $('#msg')

    if ($(Msg).attr('data-text') === $(Msg).text() || $(Msg).text().length < 5) {
      $(Msg).focus()
      return false
    }

    $.ajax({
      method: 'POST',
      url: 'https://bausano.000webhostapp.com/email.php',
      data: {
        from: 'Chat bot',
        msg: $(Msg).text()
      }
    }).done(function (rtn) {
      $(Msg).attr('contentEditable', 'false')
      $(ResponseText).text(rtn)
      $(Response).fadeIn()
      Send = false
    })
  })
})



